* {
  margin: 0;
  padding: 0;
}
.x-sheet {
  width: 100%;
  height: 100%;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  user-select: none;
}
.x-sheet-layer-bar {
  width: 100%;
  height: 100%;
}
.x-sheet-vertical-layer {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}
.x-sheet-vertical-layer-element {
  flex-grow: 0;
  overflow: hidden;
}
.x-sheet-horizontal-layer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.x-sheet-horizontal-layer-element {
  flex-grow: 0;
  flex-basis: 0;
  overflow: hidden;
}
.x-sheet-horizontal-center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.x-sheet-horizontal-center-element {
  height: 100%;
}
.x-sheet-vertical-center {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.x-sheet-vertical-center-element {
  width: 100%;
}
.x-sheet-scroll-bar-x {
  width: 100%;
  height: 17px;
  background: #dfdfdf;
  position: relative;
}
.x-sheet-scroll-bar-x-last-but {
  width: 15px;
  height: 100%;
  float: left;
  background-image: url(6143d654ce263cad76f6ab3841f1b11a.svg);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-x-next-but {
  width: 15px;
  height: 100%;
  float: right;
  background-image: url(1ec41e611e466864c7a084451f6cbcfa.svg);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-x-content {
  position: absolute;
  left: 15px;
  right: 15px;
  top: 2px;
  bottom: 2px;
}
.x-sheet-scroll-bar-x-block {
  position: absolute;
  width: 100px;
  height: 100%;
  box-sizing: border-box;
  left: 0;
  background: #ffffff;
  border-radius: 6px;
  border: 1px solid #bbb;
}
.x-sheet-scroll-bar-y {
  width: 17px;
  height: 100%;
  background: #dfdfdf;
  position: relative;
}
.x-sheet-scroll-bar-y-last-but {
  width: 100%;
  height: 15px;
  position: absolute;
  top: 0;
  background-image: url(87aeb08fa9c573408df1960dddd2f86c.svg);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-y-next-but {
  width: 100%;
  height: 15px;
  position: absolute;
  bottom: 0;
  background-image: url(5c9cd65ac12221bc4211e24bd3382f5d.svg);
  background-size: 100% 100%;
  background-color: #dfdfdf;
}
.x-sheet-scroll-bar-y-content {
  position: absolute;
  top: 15px;
  left: 2px;
  right: 2px;
  bottom: 15px;
  z-index: 2;
}
.x-sheet-scroll-bar-y-block {
  position: absolute;
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  top: 0;
  background: #ffffff;
  border-radius: 6px;
  border: 1px solid #bbb;
}
.x-sheet-el-pop-up {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  overflow: hidden;
  box-shadow: 0 2px 6px 2px rgba(60, 64, 67, 0.15);
}
.x-sheet-drag-panel {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
  cursor: move;
}
.x-sheet-drag-panel-mask {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.x-sheet-el-context-menu {
  padding: 10px;
}
.x-sheet-el-context-menu-item {
  cursor: pointer;
}
.x-sheet-el-context-menu-item.hover:hover {
  background: #efefef;
}
.x-sheet-el-context-menu-item-divider {
  display: inline-block;
  border-bottom: 1px solid #dbdbdb;
  width: 100%;
  vertical-align: middle;
  height: 0;
}
.x-sheet-color-array {
  overflow: hidden;
  width: 216px;
}
.x-sheet-color-array-item {
  width: 20px;
  height: 20px;
  float: left;
  margin: 1px;
  border-radius: 100%;
  border: 1px solid #e7e7e7;
  display: flex;
  justify-content: center;
  align-items: center;
}
.x-sheet-color-picker {
  background: #ffffff;
  cursor: default;
  user-select: none;
  padding: 0 5px 0 5px;
}
.x-sheet-color-picker-pre-view-color-point {
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background: black;
}
.x-sheet-color-picker-color-value-tips {
  font-size: 14px;
  color: #787878;
}
.x-sheet-color-picker-color-input {
  border: 1px solid #dadce0;
  width: 90px;
  height: 36px;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 0 3px 0 3px;
  font-size: 15px;
}
.x-sheet-color-picker-top {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 45px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.x-sheet-color-picker-select-color-point {
  position: absolute;
  top: -10px;
  left: -10px;
  width: 20px;
  height: 20px;
  background: #ff8000;
  border: 2px solid white;
  border-radius: 20px;
  box-shadow: 0 0 3px 1px #bdc1c6;
  cursor: pointer;
}
.x-sheet-color-picker-select-color-area {
  background: #42a642;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.x-sheet-color-picker-select-color-area.area2 {
  background: linear-gradient(to right, #ffffff, rgba(255, 255, 255, 0));
}
.x-sheet-color-picker-select-color-area.area3 {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}
.x-sheet-color-picker-center {
  position: relative;
  height: 246px;
  width: 246px;
  margin: 10px;
}
.x-sheet-color-picker-color-hux-tips {
  color: #787878;
  font-size: 13px;
  margin-bottom: 5px;
}
.x-sheet-color-picker-color-bar {
  width: 100%;
  height: 15px;
  border-radius: 15px;
  position: relative;
  background: linear-gradient(to right, #ff0000, #ff0080, #ff00ff, #8000ff, #0000ff, #0080ff, #00ffff, #00ff80, #00ff00, #80ff00, #ffff00, #ff8000, #ff0000);
}
.x-sheet-color-picker-bar-point {
  position: absolute;
  top: -5px;
  left: -10px;
  width: 20px;
  height: 20px;
  background: #ff8000;
  border: 2px solid white;
  border-radius: 20px;
  box-shadow: 0 0 3px 1px #bdc1c6;
  cursor: pointer;
}
.x-sheet-color-picker-bottom {
  margin: 10px;
}
.x-sheet-color-picker-button {
  height: 35px;
  width: 90px;
  float: right;
  cursor: pointer;
  line-height: 35px;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid #dadce0 !important;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  border-radius: 4px;
}
.x-sheet-color-picker-button.cancel {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 5px;
  background: white;
  color: #188038;
}
.x-sheet-color-picker-button.select {
  margin-right: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  background: #188038;
  color: #ffffff;
}
.x-sheet-color-picker-buttons {
  width: 100%;
  overflow: hidden;
  margin-bottom: 10px;
}
