.x-sheet-work {
  width: 100%;
  height: 100%;
}
.x-sheet-work-body {
  width: 100%;
  height: 100%;
  background: #e8e8e8;
  position: relative;
}
.x-sheet-powered-by-tips {
  position: absolute;
  right: 50px;
  bottom: 50px;
  z-index: 1;
  border-radius: 10px;
  padding: 20px;
  color: #ffffff;
  font-weight: bold;
  background: rgba(0, 0, 0, 0.8);
  pointer-events: none;
}
.x-sheet-sheet-view {
  width: 100%;
  height: 100%;
}
.x-sheet-sheet {
