}
.x-sheet-table {
  width: 100%;
  height: 100%;
  position: relative;
}
.x-sheet-table-canvas {
  display: block;
  position: absolute;
  /** flex bug chrome 79.0.3945.88 **/
  left: 0;
  top: 0;
}
.x-sheet-screen {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}
.x-sheet-screen-element {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  overflow: hidden;
}
.x-sheet-screen-element-lt {
  border-right: 1px solid #42a642;
  border-bottom: 1px solid #42a642;
}
.x-sheet-screen-element-t {
  border-bottom: 1px solid #42a642;
}
.x-sheet-screen-element-l {
  border-right: 1px solid #42a642;
}
.x-sheet-table-edit {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 1;
  cursor: default;
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  background: #ffffff;
}
.x-sheet-table-edit-input {
  width: max-content;
  min-width: 100%;
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  font-family: Arial, sans-serif;
  outline: none;
  font-size: 13px;
  background: #ffffff;
}
.x-sheet-table-y-height-light {
  width: 100px;
  height: 50px;
  background: rgba(206, 206, 206, 0.37);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  border-right: 1px solid #42a642;
}
.x-sheet-table-x-height-light {
  width: 100px;
  height: 50px;
  background: rgba(206, 206, 206, 0.37);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  border-bottom: 1px solid #42a642;
}
.x-sheet-re-sizer-vertical {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  z-index: 2;
}
.x-sheet-re-sizer-vertical .x-sheet-re-sizer-hover {
  background: rgba(111, 255, 75, 0.25);
}
.x-sheet-re-sizer-vertical .x-sheet-re-sizer-line {
  border-bottom: 2px dashed #42a642;
  box-sizing: border-box;
  position: absolute;
  height: 100%;
  bottom: 0;
}
.x-sheet-re-sizer-horizontal {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  z-index: 2;
}
.x-sheet-re-sizer-horizontal .x-sheet-re-sizer-hover {
  background: rgba(111, 255, 75, 0.25);
}
.x-sheet-re-sizer-horizontal .x-sheet-re-sizer-line {
  border-right: 2px dashed #42a642;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  right: 0;
}
.x-sheet-autofill {
  width: 0;
  height: 0;
  position: absolute;
}
.x-sheet-autofill-area {
  border: 2px dashed #42a642;
  background: rgba(181, 202, 242, 0.1);
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
@keyframes clipboard-stroke-dashoffset-animation {
  to {
    stroke-dashoffset: 19;
  }
}
.x-sheet-clipboard {
  position: absolute;
  display: block;
  width: 197.8px;
  height: 97.8px;
  left: 599.2px;
  top: 200px;
}
.x-sheet-clipboard-stroked {
  display: block;
  stroke: #4b89ff;
  fill: transparent;
  stroke-width: 3;
  stroke-dasharray: 10;
  animation: clipboard-stroke-dashoffset-animation 0.5s forwards infinite linear;
}
.x-sheet-selector {
  width: 0;
  height: 0;
  position: absolute;
}
.x-sheet-selector-area {
  border: 3px solid #42a642;
  background: rgba(181, 202, 242, 0.1);
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.x-sheet-selector-corner {
  pointer-events: auto;
  position: absolute;
  font-size: 0;
  height: 5px;
  width: 5px;
  border: 2px solid #ffffff;
  background: #42a642;
}
@keyframes copy-style-stroked-dashoffset-animation-top {
  to {
    stroke-dashoffset: 20;
  }
}
@keyframes copy-style-stroked-dashoffset-animation-left {
  to {
    stroke-dashoffset: -20;
  }
}
@keyframes copy-style-stroked-dashoffset-animation-bottom {
  to {
    stroke-dashoffset: -20;
  }
}
@keyframes copy-style-stroked-dashoffset-animation-right {
  to {
    stroke-dashoffset: 20;
  }
}
.x-sheet-copy-style {
  width: 0;
  height: 0;
  position: absolute;
}
.x-sheet-copy-style-mask {
  border: 3px solid #42a642;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.x-sheet-copy-style-svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
.x-sheet-copy-style-stroked-top {
  display: block;
  stroke: #ffffff;
  stroke-width: 7;
  stroke-dasharray: 10;
  animation: copy-style-stroked-dashoffset-animation-top 0.5s forwards infinite linear;
}
.x-sheet-copy-style-stroked-left {
  display: block;
